body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.fs {
    font-size: 1.2rem;
}

@media (min-width: 440px) {
    .fs {
        font-size: 2rem;
    }
}

.navbg {
    background-color: #232F3E;
}
.Buttons {
    display: flex;
}

.Buttons .btn {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 3rem;

    color: white;
    font-size: 1.5rem;
    cursor: pointer;
}

.Buttons .add {
    background: #37b24d;
}

.Buttons .add:hover {
    background: #40c057;
}

.Buttons .remove {
    background: #f03e3e;
}

.Buttons .remove:hover {
    background: #fa5252;
}
.counter {
    height: 300px;
    width: 300px;
    
    margin: 0 auto;
    overflow: hidden;
}

.counter-display {
    height: 150px;
}

.counter-panel {
    height: 100px;
}

.btn {
    border-radius: 0 !important;
    cursor: pointer;
    box-shadow: none !important;
}

.counter-title {
    height: 50px;
    width: 300px;
    font-weight: 500;
    font-size: 20px;
    border: none;
}

.title-input {
    text-align: center;
}
